import { createApp } from "vue";
import axios from "axios";
import { createRouter, createWebHistory } from "vue-router";
import VueAxios from "vue-axios";

import App from "./App.vue";

import AddImages from "./components/Home Page/AddImages.vue";
import UserFeed from "./components/Home Page/UserFeed.vue";
import ChatPage from "./components/ChatPage/ChatPage.vue";
import HomePage from "./components/Home Page/HomePage.vue";
import LoginPage from './components/Login Page/LoginPage.vue';
import SignIn from './components/Login Page/SignIn.vue';
import SignUp from './components/Login Page/SignUp.vue';

import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faUserSecret,
  faBars,
  faCommentDots,
  faUsers,
  faUser,
  faQuestionCircle,
  faImages,
  faTimesCircle,
  faArrowLeft,
  faArrowRight
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";

import BaseIcon from "./components/UI/BaseIcon.vue";
import BaseDialog from "./components/UI/BaseDialog.vue";
import BaseNavbar from "./components/UI/BaseNavbar.vue";

// creating routes
const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path:'/', redirect:'/home/feed' },
    {
      path: "/home",
      component: HomePage,
      children: [
        { path: "add-image", component: AddImages },
        { path: "feed", component: UserFeed },
        { path: "/join", component: LoginPage, children: [
          { path: 'signin', component: SignIn },
          { path: 'signup', component: SignUp }
        ],}
      ],
    },
    { path: "/interact", component: ChatPage },
  ],
});

library.add(
  faBars,
  faUserSecret,
  faCommentDots,
  faUsers,
  faUser,
  faQuestionCircle,
  faImages,
  faTimesCircle,
  faArrowLeft,
  faArrowRight
);


const app = createApp(App);
app.component("font-awesome-icon", FontAwesomeIcon);
app.component("base-icon", BaseIcon);
app.component("base-dialog", BaseDialog);
app.component("base-navbar", BaseNavbar);

app.use(VueAxios, axios)

app.use(router);

app.config.unwrapInjectedRef = true

app.mount("#app");